.buttonBackground {
  border-radius: 8px;
  background-color: #FFB800;
  font-size: 16px;
  height: 64px;
  padding: 0 16px;
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.buttonBackgroundMobile {
  font-size: 14px;
}

.buttonBackground:hover {
  background-color: #DCB52C;
}
